import axios from "axios";
import { useEffect, useState } from "react";
import styles from "./Products.module.css";
import {Link} from "react-router-dom";
import {connect} from "react-redux";
import { getProducts,addToCart } from '../../redux/actions';
const Products=(props) =>{
  // const [products, setProducts] = useState([]);
  useEffect(() => {
    axios
      .get("https://5d76bf96515d1a0014085cf9.mockapi.io/product")
      .then((res) => {
        // setProducts(res.data);
        props.sendProducts(res.data);
      })
      .catch((err) => console.log(err));
  }, []);
  return (
    <div>
      <div className="card-deck d-flex flex-wrap products">
        {props.products.length==0?<h1>Loading...</h1>:props.products.map((item, idx) => {
          return (
            
            <div className="card product" style={{
              width:"18rem"
            }}>
              <img className="card-img-top" src={item.preview} alt="Card image cap"/>
              <div className="card-body">
              <Link to={`/react/Products/${item.id}`}>

                <h5 className="card-title">{item.name}</h5>
                <p className="card-para card-text">Some quick example text to build on the card title and make up the bulk of the card's content. Some quick example text to build on the card title and make up the bulk of the card's content</p>
                </Link>
                <div className={styles.btn}>
                  <button onClick={()=>{props.sendToCart(item)}}>Add to Cart</button>
                </div>
              </div>
              
            </div>
            
            );
        })}
      </div>
    </div>
  );
}
const mapStateToProps=(store)=>({
  products:store.products
}); 

const mapDispatchToProps=(dispatch)=>({
  sendProducts:(payload)=>dispatch(getProducts(payload)),
  sendToCart:(payload)=>dispatch(addToCart(payload))
})

export default connect(mapStateToProps,mapDispatchToProps)(Products);