const initialState={
    userDetails:[],
    products:[],
    cart:[],
    cart_count:0
  
}

const Reducers=(state=initialState,action)=>{
    switch(action.type){
        case "GET_PRODUCTS":{
            return{
                ...state,
                products:action.payload
            }
        }
        case "ADD_TO_CART":{
            let temp_cart=[...state.cart];
            let index=temp_cart.findIndex((item)=>item.id==action.payload.id);
            if(index==-1){
                temp_cart.push({...action.payload,count:1});
            }
            else{
                temp_cart[index].count++;
            }
            
            return{
                ...state,
                cart:temp_cart,
                cart_count:state.cart_count+1
            }
        }

        default:
            return state
    }
}

export default Reducers;