import React from 'react';
import styles from './Movies.module.css';
class Movies extends React.Component {
    state = {
        movies: [],
        searchWord: "",
        searchResult: [],
        showSearch: false
      };
      componentDidMount() {
        fetch("https://www.omdbapi.com/?apikey=45f0782a&s=war")
          .then((res) => res.json())
          .then((res) => {
            this.setState({
              movies: res.Search
            });
          })
          .catch((err) => new Error("Movies not fetched"));
      }
      handleInputChange = (e) => {
        this.setState({
          searchWord: e.target.value
        });
      };
      handleSearch = () => {
        let searchedResult = this.state.movies.filter((item) => {
          let word = item.Title.toLowerCase();
          return (
            word.includes(this.state.searchWord) ||
            item.Year.includes(this.state.searchWord)
          );
        });
        this.setState({
          searchResult: searchedResult,
          showSearch: true
        });
      };
      handleClose = () => {
        this.setState({
          showSearch: false
        });
      };
      render() {
        return (
          
          <div className="App">
           
            <div className="input">
              <input
                type="text"
                onChange={(e) => {
                  this.handleInputChange(e);
                }}
              />
    
              <button onClick={this.handleSearch}>Search</button>
            </div>
    
            {this.state.showSearch ? (
              <div>
                <button onClick={this.handleClose}>Close Search</button>
                {this.state.searchResult.length === 0 ? (
                  <h1>No result Found</h1>
                ) : (
                  <div className="movies">
                    {this.state.searchResult.map((item) => {
                      return (
                        <div key={item.imdbID} className="movie">
                          <h1>{item.Title}</h1>
                          <img src={item.Poster} alt="Poster" />
                          <h2>{item.Year}</h2>
                        </div>
                      );
                    })}
                  </div>
                )}
              </div>
            ) : (
              <div className="movies">
                {this.state.movies.map((item) => {
                  return (
                    <div  key={item.imdbID} id={item.imdbID} className={`card movie ${styles.cardItem}`} style={{
                        width:"18rem"
                      }}>
                        <img className="card-img-top" src={item.Poster} alt="Card image cap"/>
                        <div className="card-body">
                          <h5 className="card-title">{item.Title}</h5>
                          <p className="hidden card-text">{item.Year}</p>
                          <a href="#" className="btn btn-primary">Go somewhere</a>
                        </div>
                      </div>
                  );
                })}
              </div>
            )}
          </div>
        );
      }
}
 
export default Movies;