import axios from 'axios';
import React, { useEffect, useState } from 'react';
import {useParams} from "react-router-dom";
import Products from '../Products/Products';
import styles from "./Product.module.css";
import store from '../../redux/store';
import {connect} from "react-redux";
import {Link} from "react-router-dom";
import { getProducts,addToCart } from '../../redux/actions';

const Product = (props) => {
    const [product,setProduct]=useState({});
    const [currentPhoto,setCurrentPhoto]=useState("");
    let {id}=useParams();
    // or
    // let {id} =props.match.params;
    let url="https://5d76bf96515d1a0014085cf9.mockapi.io/product";

    useEffect(()=>{
        axios.get(`${url}/${id}`)
        .then(res=>{
            setProduct(res.data);
            setCurrentPhoto(res.data.photos[0]);
        })
        .catch(err=>console.log(err));
    },[]);
    return( 
        <>
            <h1>Product Details - {id}</h1>
            <hr/>
            <div className={styles.Product}>
                <div className={styles.leftPane}>
                <img src={currentPhoto}/>
                </div>
                <div className={styles.rightPane}>
                    <h1>{product.name}</h1>
                    <h3>{product.brand}</h3>
                    <h3 className={styles.price}>
                        Price Rs :
                        <span>{product.price}</span>
                    </h3>
                    <div className={styles.description}>
                        <h3>Description</h3>
                        <p>{product.description}</p>
                    </div>
                    <h3>Product Preview</h3>
                    <div className={styles.photos}>    
                        {
                           product.hasOwnProperty('photos')?product.photos.map(item=><img onClick={()=>setCurrentPhoto(item)} className={styles.photo} src={item}/>):(<h3>Loading ..</h3>) 
                        }
                    </div>
                    <div className={styles.btn}>
                    <button onClick={()=>{props.sendToCart(product)}}>Add to Cart</button>
                    </div>

                </div>

            </div>
            
        </>

    );
}


const mapStateToProps=(store)=>({
    cart:store.cart
}); 

const mapDispatchToProps=(dispatch)=>({
    sendToCart:(payload)=>dispatch(addToCart(payload))

})
  
export default connect(mapStateToProps,mapDispatchToProps)(Product);