import React, { useState } from 'react';
import { Redirect } from 'react-router';
import PropTypes from "prop-types";
import styles from "./SignIn.module.css";


const SignIn = () => {
    const [username,SetUsername]=useState("");
    const [password,setPassword] =useState("");
    const [navigateUser,setNavigateUser]=useState(false);
    const user={
        name:"harsheet",
        password:"12345"
    };
    const handleSubmit=(e)=>{
        e.preventDefault();
        if(username.toLowerCase()==user.name && password==user.password){
            setNavigateUser(true);
        }
    }
    return (  
        <>
        {navigateUser&&<Redirect to="/react/products"/>}
        <h1>Sign In</h1>
        <form className={styles.form}>
            <div className="form-group">
                <label for="exampleInputEmail1">Email address</label>
                <input onChange={(e)=>SetUsername(e.target.value.toLowerCase())} type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email"/>
                <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
            </div>
            <div className="form-group">
                <label for="exampleInputPassword1">Password</label>
                <input type="password" className="form-control" id="exampleInputPassword1" placeholder="Password"/>
            </div>
            <div className="form-group form-check">
                <input type="checkbox" className="form-check-input" id="exampleCheck1"/>
                <label className="form-check-label" for="exampleCheck1">Check me out</label>
            </div>
            <button type="submit" className="btn btn-primary">Submit</button>
        </form>
        <hr/>
        <Child user={user}/>
        </>
    );
}
 
export default SignIn;

const Child = (props) => {
    return (
        <>
            <h1>Child</h1>
            {props.user.name}
            {props.user.password}
        </>
    );
}
    
Child.propTypes={
    // user:PropTypes.object,
    user:PropTypes.shape({
        name:PropTypes.string,
        password:PropTypes.any
    })
}

Child.defaultProps={
    user:{
        name:"hello",
        password:"hello"
    }
}