import { useState } from 'react';
import {Switch} from "react-router-dom";
import {Route} from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import Topbar from './components/Topbar/Topbar';
import ErrorBoundary from './components/ErrorBoundary/ErrorBoundary';
import Products from './components/Products/Products';
import  Movies from './components/Movies/Movies'; 
import Home from "./components/Home/Home";
import Product from "./components/Product/Product";
import SignIn from "./components/SignIn/SignIn";
import SignUp from "./components/SignUp/SignUp";
import Cart from "./components/Cart/Cart";

function App() {
  const [page,setPage]=useState("products");
  let showPage=(page)=>{
    setPage(page);
  }
  return (
    <div className="App">
      <ErrorBoundary>
      <Topbar showPage={showPage}></Topbar>
        <Switch>
        <Route path="/" exact component={Home}/>
          <Route path="/react" exact component={Home}/>
          <Route path="/react/Cart" component={Cart}/>
          <Route path="/react/Products" exact component={Products}/>
          <Route path="/react/Products/:id" component={Product}/>
          <Route path="/react/Movies" component={Movies}/>
          <Route path="/react/SignUp" component={SignUp}/>
          <Route path="/react/SignIn" component={SignIn}/>

          <Route component={()=><h1>404 Not Found</h1>}/>
        </Switch>
      </ErrorBoundary>
    </div>
  );
  
}

export default App;
